import React from "react";
import styled from "styled-components";
import { orderStatusChange } from "../api/order";

// -    
export default function ProgressButon(tableData: any) {
  const onSelectChange = async (e: React.ChangeEvent<HTMLSelectElement>) => {
    const result = await orderStatusChange({
      orderItemId: tableData.orderId,
      orderStatus: e.target.value,
    });

    if (result.status === 200) alert("   .");
  };

  return {
    ...tableData,
    progress_button: (
      <Select
        defaultValue={tableData.orderStatus}
        onChange={(e) => onSelectChange(e)}
      >
        <option value="DEPOSIT_CONFIRMED"></option>
        <option value="SHIPPING_COMPLETED"></option>
        <option value="WAITING_FOR_DEPOSIT"></option>
        <option value="ITEM_SHIPPED"></option>
      </Select>
    ),
  };
}

const Select = styled.select`
  width: 100px;
  padding: 5px 10px;
  border-radius: 8px;
`;
