import React, { useEffect, useState } from "react";
import { ModalWrapper } from "../styles/modal_wrapper";
import styled from "styled-components";
import ModalCloseButton from "./common/modal_close_button";
import ModalInputText from "./common/modal_input_text";

import ModalArea from "./common/modal_area";
import ModalImgAdd from "./common/modal_img_add";
import ModalButton from "./common/modal_button";
import ModalBadgeCode from "./common/modal_badge_code";
import { handleAlertModal } from "./common/table";
import { TourDetail } from "../types/tour";
import { getTourismDetailList, tourismAdd, tourismModify } from "../api/tour";
import ModalAlert from "./common/modal_alert";

interface Props {
  id: string;
  closeModal: any;
  state: "" | "";
}

// ,  
export default function TourModal({ id, closeModal, state }: Props) {
  const [data, setData] = useState<TourDetail>({
    tourismName: "",
    tourismAddress: "",
    tourismLink: "",
    tourismContact: "",
    areaCode: "",
    detailAreaCode: "",
    mapX: "",
    mapY: "",
    badgeCode: "",
    tourismImages: [],
  });
  const [alertModal, setAlertModal] = useState({
    deleteAlert: false,
  });

  const {
    detailAreaCode,
    mapX,
    mapY,
    badgeCode,
    areaCode,
    tourismName,
    tourismAddress,
    tourismContact,
    tourismImages,
    tourismLink,
  } = data;

  const inputChange = (e: React.ChangeEvent<HTMLFormElement>) => {
    let { name, value } = e.target;

    // 
    if (name.lastIndexOf("Images") !== -1) {
      const file = e.target.files[0];
      const fileUrl = URL.createObjectURL(file);

      value = tourismImages ? [...tourismImages, fileUrl] : [fileUrl];
    }

    if (name === "badgeOpenCount") value = Number(value);
    setData((prev) => ({
      ...(prev || {}),
      [name]: value,
    }));
  };

  // 
  const handleImgDelete = (url: string) => {
    let value = tourismImages.filter((urls: any) => urls !== url);
    setData((prev) => ({
      ...(prev || {}),
      tourismImages: value,
    }));
  };

  const handleCancleAlert = () =>
    handleAlertModal("deleteAlert", setAlertModal);

  const handleInputCheck = () => {
    if (
      detailAreaCode === "" &&
      mapX === "" &&
      mapY === "" &&
      badgeCode === "" &&
      areaCode === "" &&
      tourismName === "" &&
      tourismAddress === "" &&
      tourismContact === "" &&
      tourismImages.length === 0 &&
      tourismLink === ""
    ) {
      alert("   .");
      return false;
    }
    return true;
  };

  const handleFormData = () => {
    const formData = new FormData();
    formData.append("detailAreaCode", detailAreaCode);
    formData.append("mapX", mapX);
    formData.append("mapY", mapY);
    formData.append("badgeCode", badgeCode);
    formData.append("areaCode", areaCode);
    formData.append("tourismName", tourismName);
    formData.append("tourismAddress", tourismAddress);
    formData.append("tourismContact", tourismContact);
    formData.append("tourismImages", tourismImages);
    formData.append("tourismLink", tourismLink);
    return formData;
  };

  //*
  const handleTourAdd = async () => {
    const inputCheck = handleInputCheck();
    if (!inputCheck) return;

    const formData = handleFormData();

    const result = await tourismAdd(formData);
  };

  //*
  const handleTourModify = async () => {
    const inputCheck = handleInputCheck();
    if (!inputCheck) return;

    const formData = handleFormData();
    formData.append("tourismId", id);

    const result = await tourismModify(id, formData);
  };

  const handleTourData = async () => {
    if (id === "") return;
    const result = await getTourismDetailList(id);
    setData(result);
  };

  useEffect(() => {
    state === "" && handleTourData();
  }, []);

  return (
    <ModalWrapper>
      <Modal>
        <ModalCloseButton close={closeModal} />
        <form onChange={inputChange}>
          <ModalInputText
            label=""
            name="tourismName"
            value={tourismName}
            placeholder=" "
          />
          <ModalInputText
            label=""
            value={tourismAddress}
            name="tourismAddress"
            placeholder=" "
          />
          <ModalArea
            label=" "
            detailAreaCodeValue={detailAreaCode}
            areaCodeValue={areaCode}
          />
          <ModalInputText
            label=" "
            value={mapX}
            name="mapX"
            placeholder=" "
          />
          <ModalInputText
            label=" "
            value={mapY}
            name="mapY"
            placeholder=" "
          />
          <ModalInputText
            label=""
            value={tourismLink}
            name="tourismLink"
            placeholder=" "
          />
          <ModalInputText label="" value="" placeholder=" " />
          <ModalBadgeCode value={badgeCode} />
          <ModalImgAdd
            handleImgDelete={handleImgDelete}
            value={tourismImages}
            name="tourismImages"
          />
        </form>
        <ModalButton
          cancleButton={handleCancleAlert}
          addButton={state === "" ? handleTourAdd : handleTourModify}
          state={state}
        />
      </Modal>
      {alertModal.deleteAlert && (
        <ModalAlert
          close={handleCancleAlert}
          api={closeModal}
          text={`   . ?`}
        />
      )}
    </ModalWrapper>
  );
}

const Modal = styled.div`
  width: 710px;
  height: 950px;
  margin: 10px auto;
  background: ${({ theme }) => theme.colors.grayscale[7]};
  padding: 30px;
  border-radius: 12px;

  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
`;
